body{
    background-color: rgb(242, 242, 242);
}

.main-container{
    padding-top: 5vh;
    @media all and (min-width: 992px) {
        margin: 0 10vw;
	}
}

.main-navbar{
    background-color: white;
}

.loader-container{
    width: 100vw;
    height: 100vh;
    text-align: center;
    position: fixed;
    top: 0px;
    left: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.product-thumbnail{
    margin: auto;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    &.products-list-table{
        width: 100px;
        height: 100px;
    }
    &.product-detail{
        width: 300px;
        height: 300px; 
    } 
}

.product-detail-carousel{
    width: 80%;
    margin: auto;
}

.products-list-table tbody td{
    cursor: pointer;
}

.pagination-container{
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
    flex-wrap: wrap;
}